import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import { Button , Navbar , Nav , Form , FormControl } from 'react-bootstrap';
// import ServiceBox from './serviceBox';

import HomePage from './pages/homePage';
import AboutPage from './pages/aboutPage';
import ContactusPage from './pages/contactusPage';
import ServicesPage from './pages/servicesPage';

import logo from '../src/img/logo.jpeg';//logo.svg
import './App.css';



export default class App extends Component{
  homePage(){
    let dist = document.querySelector('#bodyItems');
    ReactDOM.render(<HomePage />,dist)
  }
  
  aboutPage(){
    let dist = document.querySelector('#bodyItems');
    ReactDOM.render(<AboutPage />,dist)
  }

  servicesPage(){
    let dist = document.querySelector('#bodyItems');
    ReactDOM.render(<ServicesPage />,dist)  
  }
  contactusPage(){
    let dist = document.querySelector('#bodyItems');
    ReactDOM.render(<ContactusPage />,dist) 
    }

  menuTop(){
    return(
      <div id='menuTopContainer'>
        <div id='logoContainer'>
          <img src={logo} />
          <p>Ok</p>
          {this.testBtn()}
        </div>
        <div class='homeMenuTop' id='menuTop'><p onClick={()=>this.homePage()}>الرئيسية</p></div>
        <div id='menuTop'><p onClick={()=>this.aboutPage()}>عن فروسية الزلفي</p></div>
        <div id='menuTop'><p onClick={()=>this.servicesPage()}>الخدمات</p></div>
        <div id='menuTop'><p onClick={()=>this.contactusPage()}>تواصل معنا</p></div>
      </div>
    );
  }
  imageNavigator(){
    return(<p>I'm imageNavigator..</p>);
  }
  bsNavigation(){
    return(
      <>
      
      <Nav fill variant="tabs" >{/*defaultActiveKey="/home">*/}
        <Nav.Item>
          <Nav.Link eventKey="link-0" onClick={()=>this.homePage()}><h4>الرئيسية</h4></Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-1" onClick={()=>this.aboutPage()}><h4>عن فروسية الزلفي</h4></Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-2" onClick={()=>this.servicesPage()}><h4>الخدمات</h4></Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-3" onClick={()=>this.contactusPage()}>{/*disabled>*/}
          <h3>تواصل معنا</h3>
          </Nav.Link>
        </Nav.Item>
      </Nav>
      </>
    );
  }
  servicesLinks(){
    return<div>
      {/* <ServiceBox title='التعاميم'/>
      <ServiceBox title='قائمة الموسم'/>
      <ServiceBox title='برنامج السباق'/>
      <ServiceBox title='نتائج السباق'/> */}
      
    </div>;
  }
  testBtn(){
    return (
        <Button
          onClick={() => (!1)}
          aria-controls="example-collapse-text"
          aria-expanded={1}
        >
          click
        </Button>
    );
  }
  bodyItems(){
    return(
      <div id='bodyItems'> 
      
        {/* <div id='imageNavigator'>{this.imageNavigator()}</div> */}
        <div id='servicesLinks'>{this.servicesLinks()}</div>
      </div>
    );
  }
  mainPage(){
    return(
      <div id='mainPageContainer'>

        {/* <div id='header'> {this.menuTop()} </div> */}
        <div id='header'> {this.bsNavigation()} </div>
        <div id='body'> <HomePage /> </div>
        <div id='footer'><p>Footer</p></div>

      </div>
    );
  }
  render(){
    return(this.mainPage())
  }
}