import React, { Component } from 'react';

export default class InfoBox extends Component{
    render(){
        return(
            <div id='infoBox'>
                <div id='infoTitle'><h1>{this.props.title}</h1></div>
                <div id='infoText'><h3>{this.props.text}</h3></div>
            </div>
        );
    }
}